import { useEffect, useState } from "react";
import LandService from "services/land.service";
import { Land } from "types/Land";
import { defaultUser } from "types/User";

interface dataProps {
  lands: Land[];
}

function useLandGrid() {
  const [loading, setLoading] = useState(true);
  const [data, setData] = useState<dataProps>({
    lands: [],
  });
  const [hasError, setHasError] = useState(false);
  const { lands } = data;
  const [clickedLand, setClickedLand] = useState<number>();
  const [isDrawerOpen, setIsDrawerOpen] = useState<boolean>(false);

  const handleVoteUpDown = async (
    landId: number,
    isUpVote: boolean,
    userAddress: string
  ) => {
    if (isUpVote) {
      const upVoteRes = await LandService.postUpVoteById(landId, userAddress);
      console.log("upVoteRes : ", upVoteRes);

      if (upVoteRes === "success") {
        setData({
          lands: lands.map((land, i) =>
            i === landId && land.content
              ? { ...land, upVoters: land.upVoters.concat(defaultUser) }
              : land
          ),
        });
      } else {
        alert("You already voted");
      }
    } else {
      const downVoteRes = await LandService.postDownVoteById(
        landId,
        userAddress
      );
      if (downVoteRes === "success") {
        setData({
          lands: lands.map((land, i) =>
            i === landId && land.content
              ? { ...land, downVoters: land.downVoters.concat(defaultUser) }
              : land
          ),
        });
      } else {
        alert("You already voted");
      }
    }
  };

  const calcSizeByVote = (upVote: number, downVote: number) => {
    return upVote - downVote;
  };

  const fetchData = async () => {
    try {
      const landsRes = await LandService.getAllLands();
      const { data }: { data: Land[] } = landsRes;
      console.log("FETCH ALL LANDS SUCCESS!", data);

      setData({ lands: data });
    } catch (e) {
      console.error("FAILED TO FETCH ALL LANDS!");
      setHasError(true);
    }
    setLoading(false);
  };

  useEffect(() => {
    fetchData();
  }, []);
  return {
    data,
    setData,
    loading,
    hasError,
    handleVoteUpDown,
    calcSizeByVote,
    clickedLand,
    setClickedLand,
    isDrawerOpen,
    setIsDrawerOpen,
  };
}

export default useLandGrid;
