import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { login, logout } from "stores/user.slice";
import styles from "./MetamaskAuth.module.css";

function isMobileDevice() {
  return "ontouchstart" in window || "onmsgesturechange" in window;
}

async function connect(onConnected) {
  if (!window.ethereum) {
    alert("Get MetaMask!");
    return;
  }

  const accounts = await window.ethereum.request({
    method: "eth_requestAccounts",
  });

  onConnected(accounts[0]);
}

async function checkIfWalletIsConnected(onConnected) {
  if (window.ethereum) {
    const accounts = await window.ethereum.request({
      method: "eth_accounts",
    });

    if (accounts.length > 0) {
      const account = accounts[0];
      onConnected(account);
      return;
    }

    if (isMobileDevice()) {
      await connect(onConnected);
    }
  }
}

async function checkConnectedOrNot() {
  if (window.ethereum) {
    window.ethereum.on("accountsChanged", accounts => {
      console.log(accounts);

      if (accounts && accounts[0]) {
        console.log("METAMASK IS CONNECTED!!!");
      } else {
        console.log("METAMASK IS DISCONNECTED!!");
      }
    });
  }
}

export default function MetaMaskAuth({ onAddressChanged, setIsLoggedIn }) {
  const [userAddress, setUserAddress] = useState("");
  const dispatch = useDispatch();

  useEffect(() => {
    checkIfWalletIsConnected(setUserAddress);
    checkConnectedOrNot();
    return () => checkConnectedOrNot();
  }, []);

  useEffect(() => {
    setUserAddress(userAddress);

    if (userAddress) {
      setIsLoggedIn(true);
      dispatch(login(userAddress));
    } else {
      setIsLoggedIn(false);
      dispatch(logout());
    }
  }, [userAddress]);

  return userAddress ? (
    <div>
      Connected with <Address userAddress={userAddress} />
    </div>
  ) : (
    <Connect setUserAddress={setUserAddress} />
  );
}

function Connect({ setUserAddress }) {
  if (isMobileDevice()) {
    const dappUrl = "metamask-auth.ilamanov.repl.co"; // TODO enter your dapp URL. For example: https://uniswap.exchange. (don't enter the "https://")
    const metamaskAppDeepLink = "https://metamask.app.link/dapp/" + dappUrl;
    return (
      <a href={metamaskAppDeepLink}>
        <button className={styles.button}>Connect to MetaMask</button>
      </a>
    );
  }

  return (
    <button className={styles.button} onClick={() => connect(setUserAddress)}>
      Connect to MetaMask
    </button>
  );
}

function Address({ userAddress }) {
  return (
    <span className={styles.address}>
      {userAddress.substring(0, 5)}…
      {userAddress.substring(userAddress.length - 4)}
    </span>
  );
}
