import { Avatar, Box, Button, TextField } from "@mui/material";
import { useState } from "react";
import { useSelector } from "react-redux";
import { RootState } from "stores";
import { Land } from "types/Land";
import LandService from "./../../services/land.service";

interface DrawerWriteProps {
  isVerified: boolean;
  setIsVerified: React.Dispatch<React.SetStateAction<boolean>>;
  land: Land;
  toggleDrawer: (newOpen: boolean) => () => void;
}

export default function DrawerWrite({
  land,
  isVerified,
  setIsVerified,
  toggleDrawer,
}: DrawerWriteProps) {
  const [imageUrl, setImageUrl] = useState("");

  const handleVerify = async () => {
    try {
      // const isVerified = await contractService.getContentByLandId(0);
      const isVerified = true;

      if (isVerified) {
        setIsVerified(true);
      } else {
        setIsVerified(false);
        alert("YOU ARE NOT THE OWNER OF THIS LAND!");
      }
    } catch (err) {
      console.error(err, "ERROR ON VERIFICATION");
      alert("ERROR - MAYBE... YOU ARE NOT THE OWNER OF THIS LAND");
    }
  };

  const { user, loading, error } = useSelector(
    (state: RootState) => state.user
  );

  const handleImage = async (e: any) => {
    e.preventDefault();

    const data = new FormData(e.currentTarget);
    const imageURL = data.get("image") as string;
    console.log("IMAGEURL IS ", imageURL);

    setImageUrl(imageURL);
  };

  const handleSubmit = async (e: any) => {
    e.preventDefault();

    const data = new FormData(e.currentTarget);
    const newContent = data.get("content") as string;
    console.log("CONTENT IS : ", newContent);

    const newLandDto = {
      id: land.id,
      src: imageUrl,
      content: newContent,
    };

    if (imageUrl && newContent && user && land.id) {
      try {
        const uploadRes = await LandService.patchUploadLand(
          land.id,
          newLandDto
        );
        console.log("uploadRes IS : ", uploadRes);

        if (uploadRes === "success") {
          alert("UPLOAD SUCCESS!");
          // toggleDrawer(false);
          window.location.reload();
        } else {
          alert("FAILED TO UPLOAD!, BUT NOT ERROR!");
        }
      } catch (err) {
        alert("FAILED TO UPLOAD");
        console.error(err, "FAILED TO UPLOAD");
      }
    } else {
      alert("SOMETTHING IS MISSED, TRY AGAIN");
      console.log(
        "WHAT IS MISSED?",
        "콘텐츠",
        newContent,
        "유저",
        user,
        "랜드 아이디",
        land.id,
        "이미지",
        imageUrl
      );
    }
  };

  return (
    <Box
      sx={{
        textAlign: "center",
        pt: 1,
        // backgroundColor: "#808080",
        width: "100%",
        height: "100%",
        display: "grid",
        // justifyContent: "center",
        justifyItems: "center",
        alignItems: "center",
      }}
    >
      <Box>
        {/* <Typography>you are verified! </Typography> */}
        <Box sx={{}} className="preview">
          {imageUrl && (
            <Avatar
              sx={{ width: 150, height: 150, mb: 1 }}
              alt="preview-img"
              src={imageUrl}
            />
          )}
        </Box>
        <Box component="form" onSubmit={handleImage}>
          <TextField
            name="image"
            id="image"
            variant="outlined"
            placeholder="NFT image URL"
            required
            // InputProps={{ disableUnderline: true }}
            sx={{
              pt: 1,
              pl: 0,
              borderColor: "#808080",
              minWidth: 300,
              // minHeight: 100,
              color: "white",
            }}
          />
          <Button type="submit" variant="contained">
            PREVIEW NFT
          </Button>
        </Box>

        <Box component="form" onSubmit={handleSubmit}>
          <TextField
            name="content"
            variant="outlined"
            placeholder="Write your nft project"
            multiline
            fullWidth
            required
            minRows={3}
            maxRows={15}
            // InputProps={{ disableUnderline: true }}
            sx={{
              pt: 1,
              pl: 0,
              borderColor: "#808080",
              minWidth: 600,
              minHeight: 300,
              color: "white",
            }}
          />
          <Button type="submit" variant="contained">
            Submit
          </Button>
        </Box>
      </Box>
    </Box>
  );
}
