import { Global } from "@emotion/react";
import {
  Avatar,
  Box,
  Button,
  ButtonGroup,
  CssBaseline,
  SwipeableDrawer,
  Typography,
} from "@mui/material";
import { styled } from "@mui/material/styles";
import DrawerWrite from "components/DrawerWrite/DrawerWrite";
import { useState } from "react";
import { useSelector } from "react-redux";
import { RootState } from "stores";
import { Land } from "types/Land";

const drawerBleeding = 56;

interface DrawerProps {
  index: number;
  isDrawerOpen: boolean;
  setIsDrawerOpen: React.Dispatch<React.SetStateAction<boolean>>;
  land: Land;
  handleVoteUpDown: (
    landId: number,
    isUpVote: boolean,
    userAddress: string
  ) => Promise<void>;
  window?: () => Window;
}

const Root = styled("div")(({ theme }) => ({
  height: "100%",
  backgroundColor:
    theme.palette.mode === "light"
      ? theme.palette.background.default
      : theme.palette.background.default,
}));

const StyledBox = styled(Box)(({ theme }) => ({
  backgroundColor:
    theme.palette.mode === "light" ? "rgba(255,255,255,0.8)" : "#000",
}));

const Puller = styled(Box)(({ theme }) => ({
  width: 50,
  height: 7,
  backgroundColor: theme.palette.mode === "light" ? "#808080" : "#808080",
  borderRadius: 3,
  position: "absolute",
  top: 8,
  left: "calc(50% - 15px)",
  cursor: "pointer",
}));

export default function BottomDrawer({
  index,
  window,
  isDrawerOpen,
  setIsDrawerOpen,
  land,
  handleVoteUpDown,
}: DrawerProps) {
  // This is used only for the example
  const container =
    window !== undefined ? () => window().document.body : undefined;

  const [isVerified, setIsVerified] = useState(false);
  const { user, loading, error } = useSelector(
    (state: RootState) => state.user
  );
  const myAddress = user.address;

  const toggleDrawer = (newOpen: boolean) => () => {
    setIsDrawerOpen(newOpen);

    if (isVerified) {
      setIsVerified(!isVerified);
    }
  };

  // const closeDrawerByEsc = useCallback(event => {
  //   if (event.keyCode === 27) {
  //     console.log("esc pressed");
  //     console.log(isOpen);
  //     if (isOpen) {
  //       setIsOpen(false);
  //     }
  //   }

  // }, []);

  // useEffect(() => {
  //   document.addEventListener("keydown", closeDrawerByEsc);

  //   return () => {
  //     document.removeEventListener("keydown", closeDrawerByEsc);
  //   };
  // }, [closeDrawerByEsc]);

  return (
    <Root>
      <CssBaseline />
      <Global
        styles={{
          ".MuiDrawer-root > .MuiPaper-root": {
            height: `calc(50% - ${drawerBleeding}px)`,
            overflow: "visible",
          },
        }}
      />

      <SwipeableDrawer
        container={container}
        anchor="bottom"
        open={isDrawerOpen}
        onClose={toggleDrawer(false)}
        onOpen={toggleDrawer(true)}
        variant="persistent"
        swipeAreaWidth={drawerBleeding}
        disableSwipeToOpen={false}
        // disableEscapeKeyDown={false}
        ModalProps={{
          keepMounted: true,
          disableEscapeKeyDown: false,
        }}
        BackdropProps={{ invisible: true }}
      >
        <StyledBox
          sx={{
            position: "absolute",
            top: -drawerBleeding,
            borderTopLeftRadius: 8,
            borderTopRightRadius: 8,
            // visibility: "visible", // show a little bit.
            right: 0,
            left: 0,
          }}
        >
          {/* toggle button */}
          <Puller onClick={toggleDrawer(!isDrawerOpen)} />
          {/* buttons */}
          <Box sx={{ display: "flex", justifyContent: "space-between" }}>
            <Typography sx={{ p: 2, color: "text.secondary" }}>
              show results
            </Typography>
            <Typography
              onClick={toggleDrawer(false)}
              sx={{
                p: 2,
                color: "text.secondary",
                fontWeight: "bold",
                cursor: "pointer",
              }}
            >
              close
            </Typography>
          </Box>
        </StyledBox>
        <StyledBox
          sx={{
            px: 2,
            pb: 2,
            height: "100%",
            overflow: "auto",
          }}
        >
          {/* <Skeleton variant="rectangular" height="100%" /> */}
          {land.content ? (
            <Box
              sx={{
                textAlign: "center",
                pt: 1,
                // backgroundColor: "#808080",
                width: "100%",
                height: "100%",
                display: "grid",
                // justifyContent: "center",
                justifyItems: "center",
                alignItems: "center",
              }}
            >
              {/* avatar vs img */}
              <a href={land.src} target="_blank">
                <Avatar
                  sx={{ width: 150, height: 150, mb: 1 }}
                  alt={land.src ? land.src : "No Image"}
                  src={land.src}
                />
              </a>
              {/* <a href={land.src} target="_blank">
              <img src={land.src} alt={land.alt} width={150}></img>
              </a> */}
              <Typography>#{index}</Typography>
              <Typography> comment is : {land.content}</Typography>
              <div style={{ display: "grid" }}>
                <Typography>Like : {land.upVoters.length}</Typography>
                <Typography>dislike : {land.downVoters.length}</Typography>
              </div>
              <ButtonGroup variant="contained" aria-label="">
                <Button
                  onClick={() => handleVoteUpDown(land.id, true, myAddress)}
                  color="primary"
                >
                  Like
                </Button>
                <Button
                  onClick={() => handleVoteUpDown(land.id, false, myAddress)}
                  color="secondary"
                >
                  Dislike
                </Button>
              </ButtonGroup>
            </Box>
          ) : (
            <DrawerWrite
              land={land}
              isVerified={isVerified}
              setIsVerified={setIsVerified}
              toggleDrawer={toggleDrawer}
            />
          )}
        </StyledBox>
      </SwipeableDrawer>
    </Root>
  );
}
