const getAllLands = async () => {
  return await apiRequest.get(`/land`);
};

const getLandById = async (id: string) => {
  try {
    const response = await apiRequest.get(`/land/${id}`);
    const { data } = response;
    return data;
  } catch (err) {
    return err;
  }
};

// POST

// what is this for?
const postMintedLand = async (land: any) => {
  try {
    const response = await apiRequest.post(`/land`, land);
    const { data } = response;
    return data;
  } catch (err) {
    return err;
  }
};

// PUT || PATCH

//edit land's content data
const putLand = async (id: string, body: any) => {
  try {
    const response = await apiRequest.put(`/land/${id}`, body);
    const { data } = response;
    return data;
  } catch (err) {
    return err;
  }
};

// patch land's content with image
const patchUploadLand = async (landId: number, land: any) => {
  try {
    const response = await apiRequest.patch(`/land/${landId}`, land);
    const { data } = response;
    return data;
  } catch (err) {
    return err;
  }
};

// for rent
const rentLand = async (id: string, body: any) => {
  try {
    const response = await apiRequest.put(`/land/${id}`, body);
    const { data } = response;
    return data;
  } catch (err) {
    return err;
  }
};

const closeDeadline = async (id: string) => {
  try {
    const response = await apiRequest.patch(`/land/close/${id}`, {
      closed: true,
    });
    const { data } = response;
    return data;
  } catch (err) {
    return err;
  }
};

// post vote == like and dislike
const postUpVoteById = async (landId: any, address: string) => {
  try {
    const response = await apiRequest.post(`/land/upvote?id=${landId}`, {
      address,
    });
    const { data } = response;
    return data;
  } catch (err) {
    return err;
  }
};
const postDownVoteById = async (landId: any, address: string) => {
  try {
    const response = await apiRequest.post(`/land/downvote?id=${landId}`, {
      address,
    });
    const { data } = response;
    return data;
  } catch (err) {
    return err;
  }
};

const LandService = {
  getAllLands,
  getLandById,
  patchUploadLand,
  postMintedLand,
  postUpVoteById,
  postDownVoteById,
  putLand,
  rentLand,
  closeDeadline,
};

export default LandService;
