import { createAction, createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import authService from "services/auth.service";
import { User, defaultUser } from "types/User";

export const login = createAsyncThunk(
  "users/LOGIN",
  async (address: string, { rejectWithValue }) => {
    try {
      console.log("START -- GET USER BY ADDRESS");
      const userRes = await authService.login(address);
      const user: User = userRes.data;
      console.log("SUCCESS, IT WILL STORE SOON!");

      return user;
    } catch (err) {
      console.error(err, "FAILED TO LOGIN!");
      return rejectWithValue(err);
    }
  }
);

export const logout = createAction("user/LOGOUT");

interface UserState {
  user: User;
  loading: boolean;
  error: any;
}
const initialState = {
  user: defaultUser,
  loading: false,
  error: "",
};

const usersSlice = createSlice({
  name: "user",
  initialState,
  reducers: {
    //동기 작업
    logout: state => {
      state.user = defaultUser;
      console.log("메타마스크 연결이 종료되었습니다. - 로그아웃 합니다.");
    },
  },
  extraReducers: {
    // 비동기 작업
    [login.pending.type]: state => {
      state.user = defaultUser;
      state.loading = true;
      state.error = "";
    },
    [login.fulfilled.type]: (state, action) => {
      state.user = action.payload ?? defaultUser;
      console.log("LOGIN SUCCESS! - USER DATA : ", action);
      state.loading = false;
      state.error = "";
    },
    [login.rejected.type]: (state, action) => {
      state.user = defaultUser;
      state.loading = false;
      state.error = action.payload;
    },
  },
});

export default usersSlice.reducer;
