import { Fragment } from "react";
import BottomDrawer from "../Drawer/BottomDrawer";
import styles from "./LandGrid.module.css";
import useLandGrid from "./useLandGrid";

export default function LandGrid(props: any) {
  const {
    data,
    loading,
    hasError,
    clickedLand,
    setClickedLand,
    isDrawerOpen,
    setIsDrawerOpen,
    handleVoteUpDown,
    calcSizeByVote,
  } = useLandGrid();

  const { lands } = data;

  return (
    <Fragment>
      {loading ? null : hasError ? null : (
        <div className={styles.container}>
          {lands.map((land, i) => {
            const landSizeByVote = calcSizeByVote(
              land.upVoters.length,
              land.downVoters.length
            );
            return (
              <div
                key={i}
                className={`${styles.item} ${
                  clickedLand === i ? styles.clicked : ""
                } ${
                  landSizeByVote >= 25
                    ? styles.span5
                    : landSizeByVote >= 20
                    ? styles.span4
                    : landSizeByVote >= 15
                    ? styles.span3
                    : landSizeByVote >= 10
                    ? styles.span2
                    : styles.span1
                }`}
                onClick={e => {
                  setClickedLand(i);
                  setIsDrawerOpen(true);
                }}
              >
                {land.content ? (
                  <img
                    className={styles.img}
                    src={land.src}
                    alt={land.src ? land.src : "No Image"}
                    width={
                      landSizeByVote >= 25
                        ? 350
                        : landSizeByVote >= 20
                        ? 280
                        : landSizeByVote >= 15
                        ? 210
                        : landSizeByVote >= 10
                        ? 140
                        : 70
                    }
                    height={
                      landSizeByVote >= 25
                        ? 350
                        : landSizeByVote >= 20
                        ? 280
                        : landSizeByVote >= 15
                        ? 210
                        : landSizeByVote >= 10
                        ? 140
                        : 70
                    }
                  ></img>
                ) : null}
              </div>
            );
          })}
        </div>
      )}

      {loading ? null : hasError ? null : (
        <BottomDrawer
          index={clickedLand ?? 0}
          land={lands[clickedLand ?? 0]}
          isDrawerOpen={isDrawerOpen}
          setIsDrawerOpen={setIsDrawerOpen}
          handleVoteUpDown={handleVoteUpDown}
        ></BottomDrawer>
      )}
    </Fragment>
  );
}
